.CheckoutBilling {
    &-Checkbox {
        display: inline-block;
        margin-bottom: 2.4rem;

        @include mobile {
            margin-bottom: 2.8rem;
        }

        + .CheckoutAddressBook {
            margin-top: 0;
        }
    }

    &-TermsAndConditions {
        font-weight: 700;
        font-size: 1.26rem;
        margin: 0;
        margin-bottom: 2.4rem;
        display: flex;
        align-items: center;

        @include mobile {
            font-size: 1.47rem;
            margin-bottom: 2.8rem;
        }

        &-Checkbox {
            margin: 0;
            margin-left: 1.2rem;

            @include mobile {
                margin-left: 1.4rem;
            }
        }
    }

    &-TACLabel {
        display: inline;
    }

    &-TACLink {
        color: var(--link-color);
        cursor: pointer;

        &:hover {
            text-decoration: underline;
        }

        &:focus {
            text-decoration: underline;
        }
    }
}
